.gallery__header{
    display: flex;
}

.gallery__back{
    width: 15%;
    padding: 30px 0 0 40px;
}

.gallery__tittle{
    width: 85%;
    padding: 30px 0 0 350px;
}

.gallery__tittle img{
    width: 250px;
}

.btn__small{
    width: 100px;
    height: 35px;
    text-decoration: none;
    margin: 10px 10px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 15px;
    border: none;
    border-radius: 8px;
    background-color: rgb(5, 10, 20);
    cursor: pointer;
    color: wheat;
}

.btn__small:hover{
    
    background-color: rgb(180, 11, 45);
    cursor: pointer;
    color: wheat;
}

.btn__delete{
    width: 30px;
    height: 30px;
    background: none;
    border: none;
    border-radius: 4px;
    background-image: url("/data/eliminar.png");
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 10px;
}

.btn__delete:hover{
    background-image: url("/data/eliminar hover.png");
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
}

/* --------------- MODULE ---------------*/

.gallery{
    
}

.module__container{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    display: flex;
    padding: 60px;
    flex-wrap: wrap;
    justify-content: space-around;
}

.module{
    width: 250px;
    height: 350px;
    box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.3);
    /* background-color: rgb(201, 3, 151); */
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 20px;
}

.module__img{
    width: 100%;
}

.module__name{
    font-family: 'Lato', sans-serif;
    font-size: 20px;
}

/* --------------- EMPTY STATE ---------------*/
.gallery__empty{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.gallery__emptyimg{
    width: 40%;
}

@media (max-width: 600px) {
    .gallery__back{
        width: 30%;
        padding: 30px 0 0 20px;
    }
    
    .gallery__back img{
        width: 70%;
    }

    .gallery__tittle{
        width: 70%;
        padding: 30px 0 0 30px;
    }
    
    .gallery__tittle img{
        width: 60%;
    }

