.inicio{
    width: 100%;
    height: 640px;
    /* background-color: rgb(201, 201, 91); */
    /* background-image: url('/data/fondoinicio.png');
    background-repeat: no-repeat;
    background-size: cover; */
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lato', sans-serif;
    font-size: 12px;
}

.inicio__img{
    height: 60%;
    margin: 20px 0 10px 0;
    background-size: cover;
    background-repeat: no-repeat;
}

.inicio__copy{
    margin-top: 60px;
}

.btn{
    width: 300px;
    height: 50px;
    text-decoration: none;
    margin: 20px 0 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    border: none;
    border-radius: 8px;
    background-color: rgb(5, 10, 20);
    cursor: pointer;
    color: wheat;
}


.btn:hover{
    background-color: rgb(180, 11, 45);
    cursor: pointer;
}


@media (max-width: 600px) {
    .inicio{
        height: 630px;
        background-color: rgb(201, 201, 91);
    }

    .inicio__img{
        height: 55%;
    }

    .btn{
        width: 250px;

    }
    
  }