
.create__header{
    display: flex;
}

.create__back{
    width: 15%;
    padding: 30px 0 0 40px;
}

.create__tittle{
    width: 85%;
    padding: 40px 0 0 240px;
}

.create__tittle img{
    width: 500px;
}

.create__container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-image: url("/data/fondocreate.png");
    background-repeat: no-repeat;
    background-size: cover; */
    background-color: rgb(5, 10, 20);;
}

.create{
    width: 80%;
    height: 80%;
    display: flex;
    padding: 10px;
    flex-direction: row;
    background-color: white;
    box-shadow: 0px 0px 13px 0px rgba(0,0,0,1);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
}

/* --------------- CREATE CHARACTER ---------------*/

.create__character{
    width: 40%;
    height: 480px;
    margin: 0px;
    padding: 0px;
    order: 1;
}
/* --------------- FIN CREATE CHARACTER ---------------*/

.create__options{
    width: 45%;
    height: 100%;
    order: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.create__menu{
    width: 12%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    order: 3;
}

.btn__create{
    cursor: pointer;
    width: 70px;
    height: 90px;
    margin-top: 20px;
    border-radius: 8px;
}

/* .btn__create img{
    width: 100%;
    border-radius: 8px;
} */

.btn__stepone{
    background-image: url("/data/steponebtn.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.btn__stepone:hover{
    background-image: url("/data/steponebtn hover.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.btn__steptwo{
    background-image: url("/data/steptwobtn.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.btn__steptwo:hover{
    background-image: url("/data/steptwobtn hover.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.btn__stepthree{
    background-image: url("/data/stepthreebtn.png");
    background-repeat: no-repeat;
    background-size: cover;
}

.btn__stepthree:hover{
    background-image: url("/data/stepthreebtn hover.png");
    background-repeat: no-repeat;
    background-size: cover;
}

@media (max-width: 600px) {
    .create{
        width: 80%;
        height: 90%;
        display: flex;
        flex-direction: column;
        box-shadow: 0px 0px 13px 0px rgba(0,0,0,0.3);
        border-radius: 10px;
        justify-content: center;
        align-items: center;
    }

    .create__container{
        height: 100%;   
    }
    
    /* --------------- CREATE CHARACTER ---------------*/
    
    .create__character{
        width: 40%;
        height: 400px;
        order: 1;
    }
    /* --------------- FIN CREATE CHARACTER ---------------*/
    
    .create__menu{
        width: 90%;
        height: 80px;
        margin: 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        order: 2;
    }

    .create__options{
        width: 90%;
        height: 100%;
        order: 3;
    }

    .btn__create{
        cursor: pointer;
        width: 70px;
        height: 70px;
        margin-top: 0px;
        border-radius: 8px;
        background-position: center;
    }
}