.stepthree__container{
    width: 100%;
    height: 70%;
}

.btn__medium{
    width: 200px;
    height: 45px;
    text-decoration: none;
    margin: 10px 10px 0 0;
    font-family: 'Lato', sans-serif;
    font-size: 15px;
    border: none;
    border-radius: 8px;
    background-color: rgb(5, 10, 20);
    cursor: pointer;
    color: wheat;
}

.btn__medium:hover{
    background-color:rgb(180, 11, 45);
}

.stepthree__suits{
    display: flex;
}

.stepthree__suitbtn{

    margin: 15px;
}

.stepthree__suitbtn button{
    width: 100%;
    background: none;
}

.stepthree__suitbtn img{
    width: 100%;
}

.stepthree__suitbtn--selected{

    border: olive 2px solid;
}

.stepthree__colors{
    display: flex;
}

.stepthree__colorbuttons{
    display: flex;
    width: 50%;
}

.stepthree__colorbtn{
    background: none;
    border: none;
    margin: 8px;
    border-radius: 30px;
}

.stepthree__colorbtn--selected{
    border: olive 2px solid;
}